.profile {
    width: 300px;
    margin: 15px auto;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%), 1px 4px 6px rgb(0 0 0 / 16%);
    overflow: hidden;
}

.description {
    padding: 25px 0;
    border-bottom: 1px solid #cfcfd0;
    text-align: center;
}

.avatar {
    width: 100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
    border-radius: 50%;
}

.name {
    margin: 0 0 10px;
    font-weight: 700;
}

.tag {
    margin: 0 0 10px;
    color: #768696;
}

.location {
    margin: 0;
    color: #768696;
}

.stats {
    display: flex;
    justify-content: space-around;
    text-align: center;
    background-color: #f3f6f9;
}

.item {
    width: 33.3%;
    padding: 15px;
}

.item:not(:last-child) {
    border-right: 1px solid #cfcfd0;
}

.label {
    display: block;
    margin-bottom: 5px;
    color: #768696;
}

.quantity {
    font-weight: 700;
}